.subscriptions {
  background-repeat: no-repeat;

  @media (max-width: $mobile-max-width) {
    padding-top: 15px;
    padding-bottom: 33px;

    background-image:
      url("../img/subscriptions-bg-mob.png"),
      url("../img/subscriptions-team-bg-mob.png");

    background-position:
      center bottom,
      13px 608px;
  }

  @media (min-width: $mobile-min-width + 1) and (max-width: $mobile-max-width) {
    background-image:
      url("../img/subscriptions-bg-tab.png"),
      url("../img/subscriptions-team-bg-mob.png");

    background-position:
      center center,
      center bottom -39px;
  }
}

.subscriptions__title {
  @media (max-width: $mobile-max-width) {
    margin-bottom: 27px;
  }
}

.subscriptions__subscription-times {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;

  font-size: $subscr-time-btn-fs;

  @media (max-width: $mobile-max-width) {
    margin-bottom: 30px;
  }
}

.subscriptions__time-btn {
  color: $accent;

  &:hover,
  &:focus {
    color: $accent-hover;
  }

  &:active {
    color: $accent-pressed;
  }

  &--current {
    border-bottom: 1px solid $accent;
  }
}

.subscriptions__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  @media (max-width: $mobile-max-width) {
    margin-right: -20px;
  }
}

.subscriptions__item {
  margin: 0 20px 28px 0;
}
